var getRmb=require('../../common/requset.js')
import keyboardPackage from '../../components/keyboard-package/keyboard-package.vue';
import passwordInput from '../../components/password-input/password-input.vue';
export default {
    data(){
        return {
            laiqi:'',
            type1:'买入',
            state:'',
            price:'',
            vior:'',
            x:'',
            rmb:'',
            numberList: [],
            length: 6,
            type: 'number',
            passIn: false,
            frte:true,
            checkall:'展示完整信息'
        }
    },
    components: {
    	keyboardPackage,
    	passwordInput
    },
    onLoad(option) {
        var that = this
        //获取上个页面携带的数据，并复制
        var ction = JSON.parse(option.mvvp)
        that.ction = ction
        console.log(123)
        var vior = that.ction[1]
        that.vior = that.ction[1]
        that.state = ction[0][0].order_status
        if(that.state == 101) {
            that.state = '待付款'
        }
        that.laiqi = that.ction[0][0]
        that.price = that.laiqi.sale_money
        that.x = that.laiqi.order_num
        that.rmb = getRmb.getrmb(that.price)
    },
    methods:{
        
        clo:function() {
        	this.passIn = false;
        	this.$refs['number'].close();
        	this.numberList.length= 0;
        },
        onChange(e){
        	console.log(e.show)
        	if(e.show==false){
        		this.passIn = false
        	}
            this.numberList.length = 0
        },
        onDelete() {
        	this.numberList.pop();
        },
        onConfirm() {
                if(this.numberList.length!=6){
                  uni.showToast({
                    title: '请输入六位交易密码！',
                    duration: 2000,
                    icon: 'none'
                  });
                }
              },
        onInput(val) {
            var that = this
        	that.numberList.push(val);
        	that.password = that.numberList.join().replace(/,/g, '');
        	if (that.numberList.length >= that.length) {
        		uni.request({
        			url: that.url + 'buyaffirm/',
        			method: 'POST',
        			data: {
                        order_num:that.x,
        				password: that.password
        			},
        			header: {
        				Authorization: 'JWT' + ' ' + that.global_.token
        			},
        			success(res) {
                        console.log(that.order_num)
                        
                        if(res.statusCode==400){
                            that.numberList.pop()
                            that.numberList.length= 0;
                            that.$refs.wrong.flag=false;
                            var n=res.data.data.err_num;
                            var s=5-n;
                            that.$refs.wrong.tip='剩余'+ s +'次机会';
                        }
                        if(res.statusCode==423){
                        	uni.showToast({
                        		title:'交易密码已锁定,请在今日24:00后进行交易',
                        		icon:'none',
                        		duration:2000
                        	})
                        }
                        if(res.statusCode==201){
                        	that.success=true
                        }
        				if (res.statusCode == 200) {
                            that.passIn = false;
                            that.$refs['number'].close();
        					uni.showToast({
        						title: '付款完成', 
        						duration: 2000
        					});
                            uni.navigateBack({
                                delta:2
                            })
                          
        				}
                        if (res.statusCode == 400) {
                            uni.showModal({
                                title:'未设置交易密码',
                                confirmText:'去设置',
                                success(res) {
                                    if(res.confirm == true) {
                                        uni.navigateTo({
                                            url:'../../my/trade-password/trade-password'
                                        })
                                    }
                                }
                            })
                        }
        				var page = getCurrentPages().pop();
        				if (page == undefined || page == null) return;
        			},
                   
        		});
                this.numberList.length= 0;
        	}
        },
        btn:function () {
            var that = this
            this.passIn = true;
            this.$refs['number'].open();
            this.onInput(val);
        },
        btn1:function () {
            var that = this
            that.frte=!that.frte;
            if(that.frte==false){
                that.checkall='收起';
            }else{
                that.checkall='展示完整信息'
            }
        }
    }
}
